/* ✅ Header Container */
.header-container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
  box-sizing: border-box;
}

/* ✅ Main Header Bar */
.header {
  width: 100%;
  height: 120px;
  background: rgba(255, 255, 255, 0.9);
  border: 3px solid rgb(3, 3, 36);
  border-radius: 12px;
  margin: 20px auto;
  padding: 0 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  flex-wrap: wrap;
  gap: 15px;
}

/* ✅ Left: Logo */
.logo-section {
  display: flex;
  align-items: center;
}

.logo-image {
  height: 100px;
  width: auto;
  object-fit: contain;
  border-radius: 8px;
}

/* ✅ Center: Navigation */
.nav-links {
  display: flex;
  gap: 25px;
  flex-wrap: wrap;
}

.nav-links a {
  text-decoration: none;
  font-weight: 600;
  color: black;
  font-size: 16px;
  transition: color 0.3s ease;
}

.nav-links a:hover {
  color: orange;
}

/* ✅ Right: Button (Optional) */
.freelancer-btn button {
  background-color: white;
  padding: 12px 20px;
  border-radius: 12px;
  border: 2px solid black;
  font-weight: bold;
  cursor: pointer;
  box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.2);
  transition: background-color 0.3s ease;
}

.freelancer-btn button:hover {
  background-color: #f0f0f0;
}

/* ✅ Tablet */
@media (max-width: 768px) {
  .header {
    height: auto;
    flex-direction: column;
    align-items: center;
    padding: 15px;
  }

  .nav-links {
    flex-direction: column;
    align-items: center;
    gap: 15px;
  }
}

/* ✅ Mobile */
@media (max-width: 480px) {
  .logo-image {
    height: 80px;
  }

  .nav-links a {
    font-size: 14px;
  }

  .freelancer-btn button {
    padding: 10px 16px;
    font-size: 14px;
  }
}
